#accordion_block_patterns_inserter {
  display: flex;
  align-items: center;

  .components-button.has-icon {
    padding: 3px;
  }
}

.accordion__pattern_list_wrapper {
  display: flex;
  flex-wrap: wrap;
  row-gap: 30px;

  .accordion__pattern_list_item {
    width: 33.33%;

    padding: 0 15px;

    .accordion__item_preview {
      display: block;
      width: 100%;
      outline: none;
      cursor: grab;
      height: 200px;
      overflow-y: scroll;
      border: 1px solid transparent;
      border-radius: 4px;
      transition: all 0.2s ease-in-out;

      &:hover {
        border-color: #0f172a;
        img {
          object-position: bottom;
        }
      }

      &::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
        background-color: #F1F1F1;
        border-radius: 5px;
      }

      &::-webkit-scrollbar {
        width: 6px;
        background-color: #F1F1F1;
      }

      &::-webkit-scrollbar-thumb {
        background-color: #C1C1C1;
        border-radius: 5px;
      }

      img {
        width: 100%;
        height: 200px;
        object-fit: cover;
        object-position: top;
        transition: 2.5s all ease;
      }
    }
  }


  .accordion_list_item_footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 16px;
    font-weight: 600;
    margin-top: 16px;

    .components-button.is-primary {
      border-radius: 4px;
    }
  }


  .pro-badge{
    font-size: 12px;
    background: #ffc107;
    color: #242729;
    padding: 3px 8px;
    border-radius: 3px;
    display: inline-block;
    vertical-align: middle;
    font-weight: 500;
    letter-spacing: 0.2px;
    text-decoration: none !important;
    box-sizing: content-box;
    line-height: unset;
    margin-left: 6px;
    margin-right: auto;
  }
}






